
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.container{
   box-shadow: 0px 0px 12px #B99470;
   background-color: #fff;
   border-radius: 17px;
   padding: 1rem;
}

input{
  width: 100%;
  font-size: 1.2rem;
  padding: 8px 4px;
  margin: 8px 0;
  border-radius: 8px;
  outline: none;
  background-color: #FAF8ED;
}

.btn{
  display: block;
  width: 100%;
  font-size: 1.2rem;
  margin: 8px 0;
  padding: 10px 0;
  background-color: #7A3E3E;
  color: #fff;
  border: 1px solid #A9B388;
  border-radius: 8px;
  cursor: pointer;
}

.btn:hover{
  background-color: #fff;
  color: #333;
}

.btn-outline{
  background-color: #fff;
  color: #333;
}

.btn-outline:hover{
  background-color: #7A3E3E;
  color: #fff;
}

.center{
  text-align: center;
  margin: 26px 0;
}

.p{
  margin: 10px 0;
}

.img-container{
  text-align: center;
}

.img-container img{
  height: 200px;
}






body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
